import React from 'react'
import { Table, Container } from 'react-bootstrap'
import {Row,Col} from 'react-bootstrap'
import '../style/AboutPR.css'

const AboutPR = props => {

    const {pupil} = props
    // console.log(a.sort((x,y)=> new Date(y.time) - new Date(x.time)))
    // console.log(a[0].time.slice(6))
    // console.log(a[0].time.slice(3,5))
    // console.log(a[0].time.slice(0,2))
    // console.log(new Date(a[1].time) > new Date(a[0].time))
    return (
        <div className='py-3 hdiv' style={{minHeight:"85vh"}}>
           <Row>
               <Col xs={12} sm={10} md={8} lg={8} className="examr">
               <h1 className="text-white text-center nametext">
               {pupil.surname} {" "} {pupil.name}
                </h1>
                <h5 className="text-white text-center">
                    {pupil.school} {" "} {pupil.class} o'quvchisi
                </h5>

                <h6 className="text-white">Qatnashadigan kurslari</h6>
                
                <Table striped bordered hover className="text-white apr-table">
                    <thead>
                        <tr>
                        <th>Fan</th>
                        <th>O'qituvchi</th>
                        <th>Guruh vaqti</th>
                        </tr>
                    </thead>
                    <tbody>
                    
                        {pupil.group1.teacher != undefined ?
                        <tr>
                        <td>Matematika</td>
                        <td>{pupil.group1.teacher === "Jasur" ? "Umarov Jasur" : pupil.group1.teacher === "Otabek" ? "Dustov Otabek" : pupil.group1.teacher === "Shohrux" ? "Hojiyev Shohrux" : "topilmadi"}</td>
                        <td>
                        {pupil.group1.time === "14-17" ? "14:00 - 17:00" : pupil.group1.teacher === "18-21" ? "18:00 - 21:00" : "topilmadi"}
                        </td>
                        </tr> : ""}
                        
                        {pupil.group2.teacher != undefined ? 
                        <tr>
                        <td>Fizika</td>
                        <td>{pupil.group2.teacher === "Jasur" ? "Umarov Jasur" : pupil.group2.teacher === "Otabek" ? "Dustov Otabek" : pupil.group2.teacher === "Shohrux" ? "Hojiyev Shohrux" : "topilmadi"}</td>
                        <td>
                        {pupil.group2.time === "14-17" ? "14:00 - 17:00" : pupil.group2.teacher === "18-21" ? "18:00 - 21:00" : "topilmadi"}
                        </td>
                        </tr> : ''}
                        
                    </tbody>
                    </Table>

                    <h6 className="text-white">Qatnashgan imtihon natijalari</h6>

                    <Table striped bordered hover className="text-white apr-table">
                    <thead>
                        <tr>
                        <th>Fan</th>
                        <th>Imtihon olingan vaqti</th>
                        <th>To'g'ri / Jami javoblar</th>
                        </tr>
                    </thead>
                    <tbody>
                     { pupil.exams.length != 0 ? [...pupil.exams].sort((x,y)=> new Date(x.time.slice(-4),x.time.slice(3,5)-1,x.time.slice(0,2)) -new Date(y.time.slice(-4),y.time.slice(3,5)-1,y.time.slice(0,2))).map(e=>(
                        <tr>
                            <td>{e.subject === "math" ? "Matematika" : e.subject === "phy" ? "Fizika" : ""}</td>
                            <td>{e.time}</td>
                            <td>{e.acount}/{e.qcount}</td>
                            
                        </tr>
                    )) : 
                    ["Matematika","Fizika"].map(e=>(
                    <tr>
                        <td>{e}</td>
                        <td>Imtihonda qatnashmagan</td>
                        <td>-</td>
                    </tr>
                    )) }
                    </tbody>
                    </Table>

                    <h6 className="text-white">Davomat</h6>

                    <Table striped bordered hover className="text-white apr-table">
                        <thead>
                            <tr>
                                <th>Matematikadan qoldirgan dars sanasi</th>
                                <th>Fizikadan qoldirgan dars sanasi</th>
                            </tr>
                        </thead>

                        <tbody>
                            
                            <tr>
                               <td>
                               <Container>
                               {pupil.davomat.math.length != 0 ?
                                [...pupil.davomat.math].sort((x,y)=> new Date(x.slice(-4),x.slice(3,5)-1,x.slice(0,2)) -new Date(y.slice(-4),y.slice(3,5)-1,y.slice(0,2))).map(m=>(
                                    <tr className='davtr'>
                                        {m}
                                    </tr>
                                )) : "Dars qoldirmagan" }
                               </Container>
                               </td>

                                {pupil.davomat.phy.length != 0 ?
                                pupil.davomat.phy.map(m=>(
                                    <td>{m}</td>
                                )) : <td>Dars qoldirmagan</td> }
                            </tr>
                        </tbody>
                            
                    </Table>
               </Col>
           </Row>
        </div>
    )
}

export default AboutPR
