import React , {useState, useEffect} from 'react'
import { Container, Nav, Navbar } from 'react-bootstrap'
import { firebaseConnect } from 'react-redux-firebase'
import { LinkContainer } from 'react-router-bootstrap'
import { compose } from 'redux'
import {connect} from 'react-redux'
import {Button} from 'react-bootstrap'
import '../style/AppNavbar.css'

const AppNavbar = props => {
    
    const [isAuthenticated, setIsAuthenticated] = useState(false)
    const {firebase, auth} = props

    useEffect(()=>{
        if(auth.uid){
            setIsAuthenticated(true)
        }else {
            setIsAuthenticated(false)
        }
    }, [auth])

    const onLogout = e => {
        e.preventDefault()

        firebase.logout()
    }

    
    return (
        <Navbar variant="dark" expand="lg" className="navbar">
            <Container>
                    <Navbar.Brand> <i>Buxoro Javohiri</i></Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        
                           {isAuthenticated && 
                            <Nav className="mr-auto">
                                  <LinkContainer to='/'>
                                       <Nav.Link>Dashboard</Nav.Link>
                                 </LinkContainer>
                            </Nav>
                            }

                            
                          {isAuthenticated === false &&  
                          <Nav className="ml-auto">  
                          <LinkContainer to='/' className="mx-2">
                                <Nav.Link>Home</Nav.Link>
                          </LinkContainer>

                          <LinkContainer to='/examresult' className="mx-2">
                                <Nav.Link>Imtihon Natijalari</Nav.Link>
                          </LinkContainer>

                          <LinkContainer to='/aboutpupil' className="mx-2">
                                <Nav.Link>O'quvchilar haqida</Nav.Link>
                          </LinkContainer>

                          <LinkContainer to='/admin' className="mx-2">
                                <Button variant="danger">
                                    Admin
                                </Button>
                          </LinkContainer>
                          </Nav>
                          } 

                        

                         
                        
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}

export default compose(
    firebaseConnect(),
    connect((state,props)=>({
        auth : state.firebase.auth
    }))
)(AppNavbar)