import React, {useState} from 'react'
import { firestoreConnect } from 'react-redux-firebase'
import { compose } from 'redux'
import {connect} from 'react-redux'
import {Col, Spinner, Row, Button} from 'react-bootstrap'
import '../style/ExamResults.css'
import ExamRA from './ExamRA'

const ExamResults = props => {
    const {group} = props
    const [subject, setSubject] = useState('math')
    const [teacher, setTeacher] = useState('Jasur')
    const [time, setTime] = useState('14-17')
    const [exam, setExam] = useState("03.07.2021")
    const [show, setShow] = useState(false)

    const onChangeSubject = sub => {
        setSubject(sub)
        if(group.filter(g=> g.subject === sub && g.teacher === teacher && g.time === time).length != 0 && group.filter(g=> g.subject === sub && g.teacher === teacher && g.time === time)[0].exams){
            setExam(group.filter(g=> g.subject === sub && g.teacher === teacher && g.time === time)[0].exams[0])
        }else{
            setExam(undefined)
        }
    }
    const onChangeTeach = tech => {
        setTeacher(tech)
        if(group.filter(g=> g.subject === subject && g.teacher === tech && g.time === time).length != 0 && group.filter(g=> g.subject === subject && g.teacher === tech && g.time === time)[0].exams){
            setExam(group.filter(g=> g.subject === subject && g.teacher === tech && g.time === time)[0].exams[0])
        }else{
            setExam(undefined)
        }
    }
    const onChangeTime = time => {
        setTime(time)
        if(group.filter(g=> g.subject === subject && g.teacher === teacher && g.time === time).length != 0 && group.filter(g=> g.subject === subject && g.teacher === teacher && g.time === time)[0].exams){
            setExam(group.filter(g=> g.subject === subject && g.teacher === teacher && g.time === time)[0].exams[0])
        }else{
            setExam(undefined)
        }
    }
    const onChangeExam = exam => {
        setExam(exam)
        
    }
    const onClickBtn = e => {
       e.preventDefault()
       if(exam != undefined){
           setShow(true)
       }
    }
    
    if(group){
        if(show){
            return (
                <ExamRA
                    group={group}
                    subject={subject}
                    teacher={teacher}
                    time={time}
                    exam={exam}
                    />
            )
        }else{
            return (
                <div className='headdiv py-3'>
                    <Row>
                        <Col xs={10} sm={10} md={6} lg={4} className="mx-auto examr">
                            <h3 className='text-center text-white htext'>Imtihon natijalarini ko'rish uchun quyidagilarni belgilang.</h3>
                            <p className='t-1'>Fanni belgilang.</p>
                            <select onChange={e=>onChangeSubject(e.target.value)} className="py-2 my-2 w-100">
                            <option value="math">Matematika</option>
                            <option value="phy">Fizika</option>
                            </select>
                            <br />
                            <p className='t-1'>O'qituvchingizni belgilang.</p>
                            <select onChange={e=>onChangeTeach(e.target.value)} className="py-2 my-2 w-100">
                                <option value="Jasur">Umarov Jasur</option>
                                <option value="Otabek">Dustov Otabek</option>
                                <option value="Shohrux">Hojiyev Shohrux</option>
                            </select>
                            <br />
                            <p className='t-1'>Guruhingizni belgilang.</p>
                            <select onChange={e=>onChangeTime(e.target.value)} className="py-2 my-2 w-100">
                                <option value="14-17">14:00dan 17:00gacha</option>
                                <option value="18-21">18:00dan 21:00gacha</option>
                            </select>
                            <br />
                            <p className='t-1'>Imtihon olingan vaqtni belgilang.</p>
                            <select onChange={e=>onChangeExam(e.target.value)} className="py-2 my-2 w-100">
                            
                                {group.filter(g=> g.subject === subject && g.teacher === teacher && g.time === time && g.exams.length != 0).length != 0 ? group.filter(g=> g.subject === subject && g.teacher === teacher && g.time === time).map(m=>(
                                    m.exams ? m.exams.map(ex=>(
                                        <option value={ex}>{ex}</option>
                                    )) : <option>Ma'lumot topilmadi</option>
                                )) : <option>Ma'lumot topilmadi</option>} 
                                {/* {console.log(group.filter(g=> g.subject === subject && g.teacher === teacher && g.time === time))} */}
                            </select>
                           
                            <Button onClick={onClickBtn} className="btn btn-large mt-1">
                                Natijani ko'rish
                            </Button>
                        </Col>
                    </Row>
                </div>
            )
        }
    }else{
        return <div style={{minHeight:"85vh", display:"flex", flexDirection:"column", justifyContent:"center", alignItems:"center"}} className='text-center py-5'> <Spinner animation="border" /> </div>
    }
}

export default compose(
    firestoreConnect([{collection : 'group'}]),
    connect((state, props)=>({
        group : state.firestore.ordered.group
    }))
)(ExamResults)
