import React, {useState} from 'react'
import { firestoreConnect } from 'react-redux-firebase'
import { compose } from 'redux'
import {connect} from 'react-redux'
import {Col, Spinner, Row, Button, Form} from 'react-bootstrap'
import { Table } from 'react-bootstrap'
import '../style/ExamResults.css'
import AboutPR from './AboutPR'


const AboutPupil = props => {
    const {pupils} = props
    const [name, setName] = useState('')
    const [show, setShow] = useState(false)
    const [selpup, setSelpup] = useState([])
    const [sub, setSub] = useState([])
    

    const onSubmit = e => {
       e.preventDefault()
       setSub([...pupils.filter(p=>p.name.toLocaleLowerCase() === name.toLocaleLowerCase())])
    }
    const onPupilSelected = p => {
        setSelpup(p)
        setShow(true)
    }

    if(pupils){
        if(show){
            return (
                <div>
                    <AboutPR
                    pupil={selpup} />
                </div>
            )
        }else{
            return (
                <div className='py-5 hdiv' style={{minHeight:"85vh"}}>
                    <Row>
                        <Col xs={10} sm={10} md={6} lg={4} className="mx-auto examr" >
                            <h3 className='text-center text-white'>O'quvchi ismini kiriting.</h3>
                            
                            <Form onSubmit={onSubmit}>
                                <Row>
                                    <Col xs={9} sm={9} md={9} lg={9}>
                                    <Form.Control 
                                    value={name}
                                    onChange={e=>setName(e.target.value.trim())} 
                                    type="text" 
                                    placeholder="Ismni kiriting" />
                                    </Col>
    
                                    <Col xs={3} sm={3} md={3} lg={3}>
                                    <Button onClick={onSubmit} className="btn btn-large">
                                    <i class="fa fa-search"></i>
                                    </Button>
                                    </Col>
                                </Row> <br />
                            
                            
                                <Table striped bordered hover>
                                <tbody className="text-white">
                                {sub.map(x=>(
                                 <tr>
                                    <td>{sub.indexOf(x)+1}</td>
                                    <td
                                    onClick={()=>onPupilSelected(x)} 
                                    style={{cursor:"pointer"}}>{x.surname} {" "} {x.name}</td>
                                    <td>{x.school}</td>
                                </tr>
                                ))}
                                </tbody>
                                </Table>
    
                           
                            </Form>
                            
                        </Col>
                    </Row>
                </div>
            )
        }
    }else{
        return <div style={{minHeight:"85vh", display:"flex", flexDirection:"column", justifyContent:"center", alignItems:"center"}} className='text-center py-5'> <Spinner animation="border" /> </div>
    }
}

export default compose(
    firestoreConnect([{collection : 'pupils'}]),
    connect((state, props)=>({
        pupils : state.firestore.ordered.pupils
    }))
)(AboutPupil)
